.TeamManagementWrapper{
    height: calc(100vh - 30px);
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    border: 1px solid black;
}

.TeamManagementSideBar{
    height: 100%;
    width: 15%;
    background-color: lightgray;
    box-sizing: border-box;
}

.TeamManagementOutlet{
    height: 100%;
    width: 85%;
    box-sizing: border-box;
}

