.auditDashboard_Heading{
    text-align: center;
    height: 30px;
    margin: 0;
}

.auditDashboard_Charts{
    width: 100%;
    height: calc(100% - 30px);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    overflow: auto;
}

.auditDashboard_chart{
    height: 50%;
    width: 50%;
    border: 1px solid black;
    box-sizing: border-box;
}

.auditDashboard_card{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: lightgray;
    color: black;
    text-align: center;
    box-shadow: 5px 10px 18px #888888;
    height: 100%;
    width: 33%;

}

@media screen and (max-width: 1000px) {
   
    .auditDashboard_chart{
        width: 100%;
    }
  }