.TeamCreationWrapper{
    height: calc(100% - 30px);
    width: 100%;
    position: relative;
}

.TeamCreationBottomBar{
    height: 30px;
    background-color: rgb(59, 89, 152);
}

.TeamCreationFunctionCreateWizard{
    background: #c6c8ca;
    color: black;
    text-align: center;
    position: absolute;
    bottom: 30px;
    left: 15%;
    width: 30%;
    height: auto;
}

.TeamCreationMemberWizard{
    background: #c6c8ca;
    color: black;
    text-align: center;
    position: absolute;
    top: 30%;
    left: 50%;
    width: 50%;
    transform: translateX(-40%);
    height: auto;
}

.TeamCreationInputFieldsContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.TeamCreationInputField{
    height: 50px;
    width: 30%;
    background-color: rgb(59, 89, 152);
    color: white;
    font-weight: bold;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    margin-top: 20px;
}

.TeamCreationButton{
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
}
