.home_wrapper{
    height: calc(100vh - 30px);
    width: 100vw;
    background: green;
    display: flex;
    flex-wrap: wrap;
}

.home_sideMenu{
    height: 100%;
    width: 15%;
    background: lightgray;
}

.home_Content{

    height: 100%;
    width: 85%;
    background-color: white;
    overflow: auto;
}

.link{
    text-decoration: none;
    color: black;
    width: 80%;
    display: inline-block;
    border-radius: 3px;
    color: black;
    padding: 5px;
}

.active{
    background-color: darkgray;
}
