.snagReportingWrapper {
  height: calc(100vh - 30px);
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

.snagReporting_sidebar {
  height: 100%;
  width: 15%;
  background: lightgray;
  border: 1px solid black;
  box-sizing: border-box;
}

.snagReporting_content {
  height: 100%;
  width: 85%;
  border: 1px solid black;
  box-sizing: border-box;
  overflow: hidden;
}

.fixTableHead {
  overflow-y: auto;
  height: 80%;
  box-sizing: border-box;
}

thead th {
  position: sticky;
  top: 0;
  font-size: small;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th,
td {
  padding: 8px 15px;
  font-size: small;
  border: 2px solid #1e5194;
  width: 10%;
}

th {
  background: #93b3dd;
}

.filterBox {
  position: absolute;
  height: 200px;
  width: 100%;
  background-color: linen;
  left: 0;
  font-size: small;
  font-weight: normal;
  text-align: left;
  overflow-y: auto;
}

.pagination {
  height: 5%;
  width: 100%;
  background: gray;
  border-bottom: 5px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pageItem {
  border: 1px solid black;
  background: #3B5998;
  color: white;
  height: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-left: 10px;
  cursor: pointer;
}

.borderRemove {
  border: none;
  background-color: transparent;
  color: black;
}

.borderWhite {
  border: 5px solid white;
  font-weight: bold;
}

.activeFilter {
  color: rgb(255, 0, 0);
}

.mapView {
  height: 10%;
  width: 100%;
  position: relative;
}

.snagImageContainer {
  position: absolute;
  height: 100%;
  background: lightslategrey;
  width: 40%;
  right: 0;
  top: 0;
  right: 0;
  z-index: 10000000;
  color: white;
  animation: mymove 1s;
  animation-fill-mode: forwards;
  overflow: auto;
}

@keyframes mymove {
  from {
    right: -100%;
  }

  to {
    right: 0px;
  }
}

.mapFullScreen {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.taskAssignWizard {
  position: absolute;
  z-index: 1000000;
  height: calc(100% - 30px);
  width: 20%;
  background: lightgrey;
  overflow: auto;
  top: 30px;
  right: 0;
  cursor: pointer;
}

.taskAssignWizardMapFull {
  position: absolute;
  z-index: 1000000;
  height: 100%;
  width: 20%;
  background: lightgrey;
  overflow: auto;
  right: 0;
  cursor: pointer;
}

.Snag_Reporting_TeamCreationInputFieldsContainer2{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.Snag_Reporting_TeamCreationInputField2{
  width: 30%;
}