.heading{
    text-align: center;
    margin:0;
    height: 30px;
}

.charts_wrapper{
    display: flex;
    height: calc(100% - 30px);
    flex-wrap: wrap;
    justify-content: center;
    overflow: auto;
}

.chart{
    width: 33.3%;
    height: 100%;
    border: 1px solid black;
    box-sizing: border-box;
}

.chartBottom{
    width: 25%;
    height: 100%;
    border: 1px solid black;
    box-sizing: border-box;
}